import React, { useEffect, useState } from 'react';
import { connect } from 'react-redux';
import ResizeableInputAceContainers from '../../components/resizeable/ResizeableInputAceContainers';

import * as actions from '../../store/actions/index';

import "ace-builds/src-noconflict/mode-java";
import "ace-builds/src-noconflict/theme-github";

import zenroom from 'zenroom';

const Zenroom = props => {

    const [errors, setErrors] = useState([]);
    const [result, setResult] = useState([]);

    // const script = `print("hello world from zenroom in nodejs")`;

    // options = {
    //     script: 'print("hello world")',
    //     success: () => { console.log('everything goes smooth') },
    //     error: () => { console.error('something very bad happened') }
    // }

    // zenroom.init(options).zenroom_exec()



    useEffect(() => {
        if (props.execute) {

            //save the result
            const zenResult = [];
            const printzenroom = (text) => { zenResult.push(text) }

            //Save any error
            const zenErrors = []
            const print_err_fn = (text) => { zenErrors.push(text) }

            //zenroom returns successfully
            const onSuccess = () => {
                setErrors(() => zenErrors.length ? zenErrors : []);
                setResult(() => zenResult.length ? zenResult : []);
            }

            //zenroom returns with error
            const onError = () => {
                setErrors([]);
                setResult([{ error: 'Error detected. Execution aborted.' }]);
            }

            const options = {
                script: props.contract,
                data: props.data,
                conf: null,
                keys: props.keys ? JSON.parse(props.keys) : null,
                print: printzenroom,
                print_err: print_err_fn,
                success: onSuccess,
                error: onError
            };

            zenroom.init(options).zencode_exec();

            // console.log('Executing zenroom done.');
            // console.log('saved lines: ');
            // console.log(savedLines);

            // set executing false
            props.onExecute(false);
        }
    }, [props]);

    return (
        <div className={"row justify-content-center flex-grow-1"}>
            <div className={'col-md-6'} style={{ padding: 0 }}>
                <ResizeableInputAceContainers
                    contract={props.contract}
                    contractChanged={props.onContractChanged}
                    keys={props.keys}
                    keysChanged={props.onKeysChanged}
                    data={props.data}
                    dataChanged={props.onDataChanged}
                />
            </div>
            <div className={'col-md-6'} style={{ border: '2px solid #e8e8e8', padding: 0 }}>
                <div style={{ padding: '10px 17px' }}>
                    <h2 style={{ marginBottom: '30px' }}>Result:</h2>
                    {result.length > 0 && result.map((result, index) => {
                        let displayResult = result.error
                            ?   (<div style={{ overflowWrap: 'break-word', color: 'red' }} key={index}>
                                    {result.error}
                                </div>)
                            :   (<div style={{ overflowWrap: 'break-word' }} key={index}>
                                    {result}
                                </div>)
                        return displayResult;
                        
                    })}
                    {errors.length > 0 && (
                        
                        <div style={{ marginTop: '50px', borderTop: '1px solid silver' }}>
                            <h5 style={{ color: 'red' }}>Errors</h5>
                            <ul>
                                {errors.map((error, index) => {
                                    return (
                                        <li style={{ color: 'red' }} key={index}>
                                            {error}
                                        </li>
                                    )
                                })}
                            </ul>
                        </div>
                    )}
                </div>
            </div>
        </div>
    );

}

const mapStateToProps = state => {
    return {
        contract: state.zenroom.contract,
        keys: state.zenroom.keys,
        data: state.zenroom.data,
        execute: state.zenroom.execute
    };
}

const mapDispatchToProps = dispatch => {
    return {
        onContractChanged: (contract) => dispatch(actions.changeContract(contract)),
        onKeysChanged: (keys) => dispatch(actions.changeKeys(keys)),
        onDataChanged: (data) => dispatch(actions.changeData(data)),
        onResultChanged: (result) => dispatch(actions.changeResult(result)),
        onErrorChanged: (error) => dispatch(actions.changeError(error)),
        onExecute: (executing) => dispatch(actions.changeExecute(executing))
    }
}

export default connect(mapStateToProps, mapDispatchToProps)(Zenroom);