import React from 'react';
import { Link } from 'react-router-dom';
import Navbar from 'react-bootstrap/Navbar';
import Button from 'react-bootstrap/Button';

const NavigationBar = props => {



    return (
        <Navbar variant="light" expand="md" style={{ backgroundColor: 'white' }}>
            <Navbar.Brand className={'mr-auto'} href="#home">
                <img
                    src={require('../../assets/images/logo.png')}
                    width="150"
                    height="30"
                    className="d-inline-block align-top"
                    alt="Zenroom logo"
                />
            </Navbar.Brand>
            {/* <Nav className="mr-auto">
                <Nav.Link href="#home">Home</Nav.Link>
                <Nav.Link href="#features">Features</Nav.Link>
                <Nav.Link href="#pricing">Pricing</Nav.Link>
            </Nav> */}

            <Button variant="outline-success" className="mr-sm-2" onClick={props.executeZenroom}>Play</Button>
            {!props.isAuthenticated
                ? <Button variant="outline-primary" className="mr-sm-2" onClick={props.showLogin}>Login</Button>
                : (
                    <div>
                        <Button variant="outline-success" className="mr-sm-2">Save</Button>
                        <Link to="/profile">
                            <Button variant="outline-primary" className="mr-sm-2">My Contracts</Button>
                        </Link>
                    </div>

                )}

        </Navbar>
    );
}

export default NavigationBar;