import React, { Fragment, useEffect, useState, useRef } from 'react';
import { Modal, Button, Form } from 'react-bootstrap';

const Auth = props => {
    const [validated, setValidated] = useState(false);
    const [show, setShow] = useState(props.show);
    const [isregistering, setIsregistering] = useState(false);

    const emailInput = useRef(null);
    const passwordInput = useRef(null);
    const userForm = useRef(null);

    const handleClose = () => {
        setShow(false);
        props.showLogin(false);
    };

    const userLoginHandler = (event) => {
        event.preventDefault();
        event.stopPropagation();
        setValidated(true);
        const email = emailInput.current.value;
        const password = passwordInput.current.value;
        props.auth(email, password, isregistering);
    }

    const isRegisteringHandler = () => {
        setIsregistering(!isregistering);
    }


    useEffect(() => {
        if (props.show) {
            setShow(props.show);
        }
    }, [props.show]);

    // let authRedirect = null;
    // if (this.props.isAuthenticated) {
    //     authRedirect = <Redirect to={this.props.authRedirectPath}/>
    // }

    return (

        <Modal show={show} onHide={handleClose}>
            <Modal.Header closeButton>
                {isregistering
                    ? (<Modal.Title className={'ml-5'}>Register</Modal.Title>)
                    : (<Modal.Title className={'ml-5'}>Sign in</Modal.Title>)}
            </Modal.Header>

            <Modal.Body className={'px-5 pb-1 pt-5'}>

                <Form ref={userForm} validated={validated} onSubmit={userLoginHandler}>
                    <Form.Group controlId="formBasicEmail">
                        {/* <Form.Label>Email address</Form.Label> */}
                        <Form.Control ref={emailInput} type="email" placeholder="Enter email" required />
                        {/* <Form.Text className="text-muted">
                            We'll never share your email with anyone else.
                        </Form.Text> */}
                        {/* <Form.Control.Feedback type="invalid">
                            Please enter correct email.
                        </Form.Control.Feedback> */}
                    </Form.Group>

                    <Form.Group controlId="formBasicPassword">
                        {/* <Form.Label>Password</Form.Label> */}
                        <Form.Control ref={passwordInput} type="password" placeholder="Password" minLength="8" required />
                        <Form.Control.Feedback type="invalid">
                            Please enter password with min 8 characters.
                        </Form.Control.Feedback>
                    </Form.Group>
                    {/* <Form.Group controlId="formBasicCheckbox">
                        <Form.Check type="checkbox" label="Check me out" />
                    </Form.Group> */}
                    <Button variant="primary" type="submit">
                        {isregistering
                            ? 'Register'
                            : 'Login'}
                    </Button>
                </Form>

            </Modal.Body>
            <Modal.Footer className={'px-5 pb-3 pt-0'} style={{ border: 0 }}>
                <Button variant="secondary" onClick={isRegisteringHandler}>
                    {isregistering
                        ? 'Login'
                        : 'Register'}
                </Button>
                <Button variant="secondary" onClick={handleClose}>
                    Cancel
                </Button>

            </Modal.Footer>
        </Modal>

    );
}

export default Auth;